import React from 'react';
import s from '../styles.modules.css'

export default function Card({name, img, min, max, onClose}) {
  return <div className={s.card}>
  <div className={s.cardContainer}>
  <button className={s.close} onClick ={onClose}> X </button>
  <h4 className={s.city}>{name || 'Agregar Nombre de Ciudad'}</h4>
  
  <div className={s.min}>
    <p>Min°</p> 
    <h5>{min}</h5>
  </div>

  <div className={s.max}>
    <p>Max°</p> 
    <h5>{max}</h5>
    </div>  

    <div className={s.icon}>
    <img src={`http://openweathermap.org/img/wn/${img}@2x.png`} alt="Not Found"/>
    </div>
    </div>
  </div>
}