import React from 'react';
import Card from './Card';
import s from '../styles.modules.css'


export default function Cards({cities}) {
  // acá va tu código
  // tip, podés usar un map
  return <div className={s.cards}>
    {
    cities.map((e) => 
      <Card
      key = {e.id}
      max= {e.main.temp_max}
      min= {e.main.temp_min}
      name= {e.name}
      img= {e.weather[0].icon}
      onClose={() => alert(e.name)}
      />
    )}
  </div>
};